html {
    font-size: 16px;
}

html,
body {
    margin: 0;
    padding: 0;

    font-family: Montserrat, sans-serif;

    background-color: #F9F9FA;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

ul,
li {
    margin: 0;

    list-style: none;
}

a,
a:link,
a:visited,
a:hover {
    text-decoration: none;
}

button {
    font: inherit;
}

input {
    font: inherit;

    outline: none;
}

[data-theme='light'] {
    --text-color: black;
    --bg-color: white;
    --secondary-bg-color: #8bc34a;
    --accent-bg-color: #b8d7ee;
    --secondary-border-color: #407224;
    --secondary-hover-bg-color: #699932;
    --input-bg-color: #ecf0f5;
}

:root:not([data-theme]),
[data-theme='dark-cyan'] {
    --text-color: white;
    --bg-color: #292b41;
    --secondary-bg-color: #2ea488;
    --accent-bg-color: #b774a9;
    --secondary-border-color: #95efda;
    --secondary-hover-bg-color: #257f6a;
    --input-bg-color: #e2e6ec;
}

@keyframes slide-up {
  from {
    opacity: 0;

    translate: 0 100px;
  }

  to {
    opacity: 1;

    translate: 0 0;
  }
}
